import { makeStyles } from "@material-ui/core/styles";

import bg from "../imgs/homebg.jpg";

const useStyles = makeStyles((theme) => ({
  container: {
    position: "relative",
    width: "100%",
    height: "100%",
    boxShadow: " inset 0px 7px 10px 0px black",
  },
  home: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    width: "100%",
    height: "100vh",
    background: `linear-gradient(rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.5)), url(${bg})`,
    backgroundPosition: "center",
    backgroundSize: "cover",
    backgroundAttachment: "fixed",
    overflow: "hidden",
    flexDirection: "column",
    // position: "absolute"
  },
  notfound: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    width: "100%",
    height: "100vh",
    backgroundPosition: "center",
    backgroundSize: "cover",
    backgroundAttachment: "fixed",
    overflow: "hidden",
    flexDirection: "column",
    // position: "absolute"
  },
  typo: {
    marginRight: theme.spacing(5),
    marginLeft: theme.spacing(5),
    textTransform: "uppercase",
    fontFamily: "'Odibee Sans', cursive",
    fontSize: "2em",
    [theme.breakpoints.up("sm")]: {
      fontSize: "3rem",
    },
    letterSpacing: theme.spacing(1),
    paddingBottom: theme.spacing(4),
  },
  typodesc: {
    color: "white",
    textTransform: "uppercase",
    // fontWeight: "bold",
    fontSize: "1em",
    [theme.breakpoints.up("sm")]: {
      fontSize: "1.2em",
    },
    fontFamily: "Montserrat, sans-serif",
  },
  butcon: {
    width: "20%",
    minWidth: "200px",
    // backgroundColor: "#ff4e00",
    // backgroundImage: "linear-gradient(90deg, #ff4e00 0%, #ec9f05 74%)",
  },
  butitm: {
    borderRadius: "2rem",
  },
  shadow: {
    height: "100vh",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
  textrespo: {
    [theme.breakpoints.down("sm")]: {
      fontSize: ".8rem !important",
    },
  },
  space: {
    padding: theme.spacing(2, 0),
  },
  pic: {
    width: "10rem",
    height: "10rem",
    border: "3px solid #EE5522",
    borderRadius: "50%",
    marginBottom: theme.spacing(2),
  },
  defaultlogo: {
    width: "10rem",
    height: "10rem",
  },
  ppic: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
  cbg: {
    width: "100%",
    backgroundColor: "#5A1186",
    boxShadow: "inset 0px 0px 10px 0px black",
  },
}));
export default useStyles;
