import "./App.css";
import AppBody from "./Content/AppBody.js";
import {
  MuiThemeProvider,
  createMuiTheme,
} from "@material-ui/core/styles";

const theme = createMuiTheme({
  palette: {
    primary: {
      main: "#251056",
      contrastText: "white",
    },
    secondary: {
      main: "#dc004e",
      contrastText: "#343f56",
    },

  },
});

function App() {
  return (
    <div className="bg">
      <MuiThemeProvider theme={theme}>
        <AppBody />
      </MuiThemeProvider>
    </div>
  );
}

export default App;
