/* eslint no-eval: 0 */
import { React, useEffect } from "react";
import TextField from "@material-ui/core/TextField";
import useStyles from "../styling/inputstyle";
import ImageUploader from "react-images-upload";
import { Typography } from "@material-ui/core";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import AOS from "aos";
import "aos/dist/aos.css";

function GetIputs(props) {
  useEffect(() => {
    AOS.init({
      once: true,
    });
  }, []);
  const classes = useStyles();
  const { input, setValue } = props;
  if (input.name === "uplogo") {
    return (
      <ImageUploader
        withIcon={true}
        buttonText="Choose images"
        onChange={props.setPic}
        name={input.name}
        imgExtension={[".jpg", ".gif", ".png", ".gif"]}
        maxFileSize={5242880}
        singleImage={true}
        withPreview={true}
        // required={true}
        label={"You Can Upload Your Logo later, Not required"}
        labelStyles={{ color: "orange" }}
        data-aos="fade-up"
        data-aos-duration="1000"
      />
    );
  }
  if (input.radio) {
    return (
      <>
       <Typography
          className={classes.textspan}
          data-aos="fade-up"
          data-aos-duration="1000"
        >
          {input.name}
        </Typography>
      <RadioGroup
        aria-label="blablaa"
        name="sds"
        value={eval("props." + input.value)}
        onChange={props.setValue}
        key="radio"
      >
        <FormControlLabel
          value="Is"
          control={<Radio />}
          label="Idea stage"
          key="Is"
        />
        <FormControlLabel value="proto" control={<Radio />} label="Prototype" key="proto"/>
        <FormControlLabel
          value="MVP"
          control={<Radio />}
          label="Minimum viable product (MVP)"
          key="MVP"
        />
        <FormControlLabel
        key="PBS"
          value="PBS"
          control={<Radio />}
          label="Product already being sold on the market"
        />
      </RadioGroup>
      </>
    );
  }
  if (props.stype !== "inelab") {
    if (input.multiline)
      return (
        <TextField
          data-aos="fade-up"
          data-aos-duration="1000"
          size="small"
          key={input.id}
          label={input.name}
          type={input.type}
          multiline={input.multiline}
          rows={input.row}
          name={input.value}
          value={eval("props." + input.value)}
          onChange={setValue}
          variant="outlined"
          required={true}
          disabled={input.preview}
          error={
            Object.keys(props.Serrors).length > 0
              ? eval("props.Serrors." + input.value) === ""
                ? false
                : true
              : false
          }
          helperText={
            Object.keys(props.Serrors).length > 0
              ? eval("props.Serrors." + input.value) === ""
                ? false
                : eval("props.Serrors." + input.value)
              : false
          }
        />
      );
    else
      return (
        <TextField
          size="small"
          data-aos="fade-up"
          data-aos-duration="1000"
          key={input.id}
          label={input.name}
          type={input.type}
          name={input.value}
          value={eval("props." + input.value)}
          onChange={setValue}
          variant="outlined"
          disabled={input.preview}
          required={true}
          error={
            Object.keys(props.Serrors).length > 0
              ? eval("props.Serrors." + input.value) === ""
                ? false
                : true
              : false
          }
          helperText={
            Object.keys(props.Serrors).length > 0
              ? eval("props.Serrors." + input.value) === ""
                ? false
                : eval("props.Serrors." + input.value)
              : false
          }
        />
      );
  } else {
    return (
      <div className={classes.divinelab}>
        {/* <span className={classes.textspan}></span> */}
        <Typography
          className={classes.textspan}
          data-aos="fade-up"
          data-aos-duration="1000"
        >
          {input.name}
        </Typography>
        <TextField
          size="small"
          data-aos="fade-up"
          data-aos-duration="1000"
          key={input.id}
          type={input.type}
          multiline={input.multiline}
          rows={input.row}
          name={input.value}
          value={eval("props." + input.value)}
          onChange={setValue}
          required={true}
          variant="outlined"
          disabled={input.preview}
          error={
            Object.keys(props.Serrors).length > 0
              ? eval("props.Serrors." + input.value) === ""
                ? false
                : true
              : false
          }
          helperText={
            Object.keys(props.Serrors).length > 0
              ? eval("props.Serrors." + input.value) === ""
                ? false
                : eval("props.Serrors." + input.value)
              : false
          }
        />
      </div>
    );
  }
}
export default GetIputs;
