import useStyles from "../../styling/style";
import { React, useEffect } from "react";
import Typography from "@material-ui/core/Typography";
import { useHistory } from "react-router-dom";
import Button from "@material-ui/core/Button";
import Footer from "../Footer";
import { Contactus } from "./Contactus/Contactus";
import { withStyles } from "@material-ui/core/styles";
import { Container, Grid } from "@material-ui/core";
import { Featuers } from "./Features";
import AOS from "aos";
import "aos/dist/aos.css";

function Home() {
  useEffect(() => {
    AOS.init();
  }, []);

  const ColorButton = withStyles((theme) => ({
    root: {
      fontFamily: "Montserrat, sans-serif",
      fontWeight: "bold",
      color: theme.palette.error.main,
      border: theme.palette.error.main + " 1px solid",
      "&:hover": {
        transition: theme.transitions.create(
          ["background-color", "fontWeight", "color", "letter-spacing"],
          {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.complex,
          }
        ),
        color: "#FFFFFF",
        fontWeight: "bold",
        letterSpacing: 2,
        backgroundColor: "#ee5522",
      },
    },
  }))(Button);
  const history = useHistory();
  const classes = useStyles();
  return (
    <>
      <div className={classes.container}>
        <div className={classes.home}>
          {/* <Grid container align="center" justify="center">
            <Grid container item xs={12} md={6} justify="center"> */}
          <Container maxWidth="md" align="center" justify="center">
            <Typography
              variant="h1"
              data-aos="zoom-in"
              data-aos-duration="1000"
              className={classes.typo}
              color="primary"
              align="center"
              style={{ color: "#ee5522" }}
            >
              BRING YOUR STARTUP IDEAS TO LIFE
            </Typography>
            <Typography
              data-aos="zoom-in"
              data-aos-duration="1100"
              variant="h6"
              className={classes.typodesc}
           
              align="center"
              paragraph
            >
              Accelerate the economic and human development of the African
              continent, in a sustainable and responsible perspective through
              applied research, innovation and entrepreneurship.
            </Typography>
            <div className={classes.butcon}>
              <ColorButton
                fullWidth
                data-aos="fade-up"
                data-aos-duration="1500"
                size="large"
                // color="secondary"
                variant="outlined"
                className={classes.butitm}
                onClick={() => {
                  history.push("/Apply");
                }}
              >
                Apply Now
              </ColorButton>
            </div>
          </Container>
          {/* </Grid>
          </Grid> */}
        </div>
        <div>
          {/* <div className={classes.shadow}>
            <h1>Features and Contact Goes Here</h1>
          </div> */}
          <Featuers />
          {/* <div className={classes.shadow}>
            <h1>Features and Contact Goes Here</h1>
          </div> */}
          <div className={classes.cbg}>
            <Contactus />
          </div>
          <div className={classes.shadow}>
            <h1>Features and Contact Goes Here</h1>
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
}
export default Home;
