import { React, useEffect } from "react";
import formsvg from "../imgs/pitch.svg";
import Grid from "@material-ui/core/Grid";
import "../../App.css";
import PutInputs from "../component/Inputs";
import useStyles from "../styling/inputstyle";
import { Step1 } from "../inputdb/Step1";
import AOS from "aos";
import "aos/dist/aos.css";

function Signup(props) {
  useEffect(() => {
    AOS.init();
  }, []);

  const classes = useStyles();
  return (
    <div className={classes.root}>
      <Grid
        container
        direction="row-reverse"
        alignItems="center"
        spacing={3}
        style={{ padding: "2rem" }}
      >
        <Grid item xs={12} md={6}>
          <div className={classes.header}>
            <span className={classes.formhead}>Sign Up</span>
          </div>
          <PutInputs inputs={Step1} {...props} />
        </Grid>
        <Grid item xs={12} md={6} className={classes.bp}>
          <img
            data-aos="fade-right"
            src={formsvg}
            alt="sing"
            className={classes.ssvg}
          />
        </Grid>
      </Grid>
    </div>
  );
}

export default Signup;
