/* eslint no-eval: 0 */
import TextField from "@material-ui/core/TextField";
import useStyles from "../styling/inputstyle";
import Radio from "@material-ui/core/Radio";
import RadioGroup from "@material-ui/core/RadioGroup";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import { Typography } from "@material-ui/core";

function GetPreview(props) {
  const classes = useStyles();
  const { input } = props;
  if (input.radio) {
    return (
      <>
       <Typography
          className={classes.textspan}
        >
          {input.name}
        </Typography>
      <RadioGroup
        aria-label="blablaa"
        name="sds"
        value={eval("props." + input.value)}
        onChange={props.setValue}
        key="radio"
      >
        <FormControlLabel
          value="Is"
          control={<Radio />}
          label="Idea stage"
          key="Is"
          disabled
        />
        <FormControlLabel value="proto" control={<Radio />} label="Prototype" key="proto" disabled/>
        <FormControlLabel
          value="MVP"
          control={<Radio />}
          label="Minimum viable product (MVP)"
          key="MVP"
          disabled
        />
        <FormControlLabel
        key="PBS"
          value="PBS"
          control={<Radio />}
          label="Product already being sold on the market"
          disabled
        />
      </RadioGroup>
      </>
    );
  }
  return input.parag === false ? (
    <div style={{flexGrow: 1, minWidth:"40%" , padding : "10px"}}>
    <div className={classes.divinelab} >
      <TextField
        size="small"
        key={input.id}
        label={input.name}
        type={input.type}
        name={input.value}
        value={eval("props." + input.value)}
        variant="outlined"
        disabled={input.preview}
        />
    </div>
        </div>
  ) : (
    <div className={classes.divinelab}>
       <Typography
          className={classes.textspan}
        >
          {input.name}
        </Typography>
      <p style={{ lineBreak: "anywhere" }}>{eval("props." + input.value)}</p>
    </div>
  );
  
}
export default GetPreview;
